@font-face {
  font-family: 'Bold';
  src: local('Poppins-Bold'), 
  url(./fonts/Poppins-Bold.ttf) format('truetype');
}

@font-face {
  font-family: 'BoldItalic';
  src: local('Poppins-BoldItalic'), 
  url(./fonts/Poppins-BoldItalic.ttf) format('truetype');
}

@font-face {
  font-family: 'ExtraBoldItalic';
  src: local('Poppins-ExtraBoldItalic'), 
  url(./fonts/Poppins-ExtraBoldItalic.ttf) format('truetype');
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Bold";
  font-size: 100%;
}

body {
  height: 100vh;
  background-color: hsl(0, 0%, 94%);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}

.app {
 width: 52.2rem ;
 height: 40.6rem ;
 background-color:  hsl(0, 0%, 100%);
 border-radius: 4% 4% 25% 4%;
 border: none;
padding: 3.5rem;
box-shadow: rgba(0, 0, 0, 0.03) 0px 0px 0px 1px;

}

form {
  display: flex;
  flex-direction: column;
 margin: 0;
 
}

.input-container {
  display: flex;
  gap: 2rem;
  min-height: 133px;
 
}


form label {
  display: flex;
  flex-direction: column;
}

.error > span {
 color: hsl(0, 100%, 67%);
}

.error .input {
  border-color:hsl(0, 100%, 67%);
  outline-color: hsl(0, 100%, 67%);
}


label > span {
  text-transform: uppercase;
  font-weight: 700;
  color: hsl(0, 1%, 44%);
  font-size: 14px;
  letter-spacing: 3px ;
  margin-bottom: 8px;
}


.input {
  font-size: 2rem;
  padding: 16px;
  width: 10rem;
  border-radius: 7px;
  border: 1px solid hsl(0, 0%, 86%);
  font-weight: 900;
  cursor: pointer;
  outline-color: hsl(259, 100%, 65%);
  
}

.input:hover {
  border-color: hsl(259, 100%, 65%);
}

.error-message {
color: hsl(0, 100%, 67%);
font-style: italic;
font-size: 12px;
margin-top: 7px;

}


.btn-container {
  position: relative;
  display: flex;
  justify-content: end;
}

.btn-container::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 86.7%;
  border-top: 2px solid hsl(0, 0%, 94%);
}

button {
 
  height: 6rem;
  width: 6rem;
  border-radius: 50%;
  border: none;
  background-color: hsl(259, 100%, 65%);
  cursor: pointer;
  transition: all 0.3s;
  
}

button:hover {
  background-color: hsl(0, 0%, 8%);
}

.result-container p {
font-family: 'ExtraBoldItalic';
font-size: 5rem;
line-height: 1.2;
}

.result-container p span {
  color: hsl(259, 100%, 65%);
}

@media all and (max-width: 50em ) {
  .app {
   width: 37.5rem;
   padding: 3rem 1.5rem;
  }

  .btn-container {
    justify-content: center;
    margin: 2rem 0;
  }

  .btn-container::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    border-top: 2px solid hsl(0, 0%, 94%);
  }

  button {
    z-index: 10;
  }
}

@media all and (max-width: 37.5em ) {

  html {
    
    font-size: 70%;
  }

  .app {
    height: 586px;
    padding: 4rem 2rem;
  }

  .btn-container {
   margin-bottom: 3.5rem;
  }

 


}

@media all and (max-width: 28em ) {
  html {
    font-size: 55%;
  }

.app {
  width: 343px;
  height: 480px;
  padding-top: 5.5rem;
}

  .btn-container {
    margin-top: 0;
   }

   button svg {
    width: 2.7rem;
    height: 2.7rem;
   }
 
}